@import '~node_modules/modern-normalize/modern-normalize.css';

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
button {
  margin: 0;
  padding: 0;
}

*::after,
*::before {
  box-sizing: inherit;
}

li {
  list-style-type: none;
}

a,
address {
  text-decoration: none;
  display: block;
}

.wrapper {
  min-height: 100vh;
  background: url('../images/mainBG.jpg') center no-repeat;
  background-size: cover;
  font-family: "Roboto", sans-serif;

}

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
}

.dating-page {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  margin-top: 100px;
}

.page__form {
  width: 540px;
  margin: 0 auto;
  position: relative;
}

.form__logo {
  width: 100%;
  height: 100px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 0 0;
  background: #fff url(../images/logo.png) center no-repeat;
  background-size: 250px;
}

.form__progress {
  position: relative;
  width: 100%;
  height: 5px;
  background: #d4d4d4;
}

.page__progress__filled {
  height: 5px;
  position: absolute;
  background: #ef0752;
}

.form-list {
  display: flex;
  flex-flow: row nowrap;
  overflow: hidden;
  align-items: flex-start;
}

.slider__item {
  width: 540px;
  flex: 1 0 100%;
  padding: 30px 40px;
  border-radius: 0 0 8px 8px;
  border: 1px solid #dcdcdc;
  background-color: #ffffff;
  color: #333333;
  transition: all .5s;
}

.main-title, .sub-title {
  margin: 0 0 20px;
  font-size: 26px;
  font-weight: 500;
  line-height: 36px;
  text-align: center;
}

.quiz__time {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quiz__time img {
  width: 30px;
  margin-right: 5px;
}

.quiz__time p {
  display: flex;
  align-items: center;
  margin: 0;
}

.quiz__description {
  padding-bottom: 21px;
}

.quiz__content {
  margin: 20px auto;
  color: #333333;
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
  text-align: center;
}

.button, .view__more {
  cursor: pointer;
  display: block;
  width: 220px;
  margin: 0 auto;
  padding: 20px 0;
  border-radius: 8px;
  background-color: #ef0752;
  text-align: center;
  color: #ffffff;
  font-size: 18px;
  font-weight: 900;
  text-transform: uppercase;
  text-decoration: none;
  border: 0;
}

.button:hover, .view__more:hover {
  -webkit-box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
}

.view__more {
  margin-top: 20px;
}

.answers-list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-left: calc(-1 * 15px);
  margin-top: calc(-1 * 15px);
}

.answers__item {
  cursor: pointer;
  -ms-flex-preferred-size: calc(100% / 4 - 15px);
  flex-basis: calc(100% / 4 - 15px);
  margin-left: 15px;
  margin-top: 15px;
  border-radius: 5px;
  -webkit-box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);

}

.answers__item:hover {
  -webkit-box-shadow: 0px 0px 16px -1px #000000;
  box-shadow: 0px 0px 16px -1px #000000;
}

.step {
  text-align: center;
  margin-bottom: 10px;
}

.answers__item--img {
  margin: 0 0 10px 0;
  border-radius: 5px 5px 0 0;
}

.answers__item p {
  text-align: center;
  margin-bottom: 10px;

}

.age-input {
  width: 100%;
  padding: 5px 5px;
  margin-bottom: 15px;
  border: inherit;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number] {
  -moz-appearance: textfield;
}

//=========description__analyzer=========
.description__analyzer {
  margin-bottom: 12px;
  color: #1c1c1c;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}

.loading {
  position: relative;
  width: 100%;
}

.loading__bar {
  position: absolute;
  width: 0;
  height: 18px;
  background-image: linear-gradient(270deg, #ef0752 0%, #f10f44 100%);
  transition: width .2s;
  border-radius: 9px;
}

.loading__analyzing {
  margin-top: 50px;
  color: #1c1c1c;
  font-size: 26px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
}

//====LOADING SPINNER==========
.loading__spinner {
  display: none;
  //display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #f10f44;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #f10f44 transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.spinner__description {
  margin-top: 15px;
  color: #1c1c1c;
  font-size: 16px;
  font-weight: 400;
  text-align: center;

}

//========REVIEW==================
.review {
  display: none;
  flex-flow: column;
  align-items: center;
}

.review__logo {
  width: 200px;
  margin: 20px 0;
}

.rating__votes {
  color: #333333;
  font-size: 13px;
  font-weight: 400;
}

.review__button {
  margin: 20px 0;
}

.review__description {
  text-align: center;
}

.rating__score {
  margin-right: 10px;
}

.review__link {
  margin-bottom: 20px;
  color: #0a9ce5;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.review__rating {
  display: flex;
  margin: 10px 0;
  flex-flow: row nowrap;
  font-size: 20px;
  color: #ef0752;
}

.rating__stars {
  width: 100px;
  position: relative;
  display: flex;
  flex-flow: row nowrap;
}

.stars__filled {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

.rating__propose {
  margin-top: 10px;
}

.rating__propose p:first-child {
  margin-bottom: 7px;
}

.rating__propose img {
  margin-right: 5px;
}

//====FOOTER=====================
.footer{
  width: 540px;
  margin: 0 auto;
  position: relative;
}
.footer__menu {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__menu-item:not(:last-child) {
  margin-right: 10px;
}

.footer__menu-link {
  font-family: "Roboto", sans-serif;
  padding: 22px 0 20px 0;
  font-size: 14px;
  color: #ffffff;
}

.footer__menu-link:hover {
  text-decoration: underline;
  color: #ffffff;
}

//errer===========
._backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

._is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal__error {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 520px;
  height: 400px;
  background: #ffffff;
  -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding: 40px 40px;
}

.modal__error {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo__error {
  width: 100%;
  height: 100px;
  border-radius: 8px 8px 0 0;
  background: #fff url(../images/logo.png) center no-repeat;
  background-size: 250px;
}

.message__error {
  text-align: center;
  margin: 20px 0;
}

//=====MEDIAQUERY==========
@media (max-width: 540px) {
  .container {
    margin: 0;
    padding: 30px 0;

  }
  .wrapper {
    padding: 0;
    background: none;
  }
  .wrapper::before {
    display: none;
  }

  .slider {
    height: calc(100vh - 54px);
  }
  .dating-page {
    width: 100%;
    margin-top: 10px;
  }
  .form__logo {
    width: 100%;
    height: 50px;
    border-radius: 8px 8px 0 0;
    background: #fff url(../images/logo.png) center no-repeat;
    background-size: 250px;
  }
  .slider__item {
    width: 100%;
    padding: 15px;
    border-radius: 0;
    border: none;
  }
  .main-title, .sub-title {
    margin-top: 0;
  }
  .answers-list {
    width: auto;
    padding: 0;
  }
  .loading {
    width: 100%;
  }
  .footer {
    border-top: 1px solid #000000;
  }
  .footer__row {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .footer__menu {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .footer__menu-item {
    margin: 0;
  }
  .footer__menu-link {
    color: #000000;
  }
}
