.wrapper {
  min-height: 100vh;
  background: url("mainBG.d5ba7f8d.jpg") center / cover no-repeat;
  font-family: Roboto, sans-serif;
}

.container {
  min-height: 100vh;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
  position: relative;
}

.dating-page {
  flex: auto;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

.page__form {
  width: 540px;
  margin: 0 auto;
  position: relative;
}

.form__logo {
  width: 100%;
  height: 100px;
  background: #fff url("logo.27288c36.png") center / 250px no-repeat;
  border-radius: 8px 8px 0 0;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .1);
}

.form__progress {
  width: 100%;
  height: 5px;
  background: #d4d4d4;
  position: relative;
}

.page__progress__filled {
  height: 5px;
  background: #ef0752;
  position: absolute;
}

.form-list {
  flex-flow: row;
  align-items: flex-start;
  display: flex;
  overflow: hidden;
}

.slider__item {
  width: 540px;
  color: #333;
  background-color: #fff;
  border: 1px solid #dcdcdc;
  border-radius: 0 0 8px 8px;
  flex: 1 0 100%;
  padding: 30px 40px;
  transition: all .5s;
}

.main-title, .sub-title {
  text-align: center;
  margin: 0 0 20px;
  font-size: 26px;
  font-weight: 500;
  line-height: 36px;
}

.quiz__time {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.quiz__time img {
  width: 30px;
  margin-right: 5px;
}

.quiz__time p {
  align-items: center;
  margin: 0;
  display: flex;
}

.quiz__description {
  padding-bottom: 21px;
}

.quiz__content {
  color: #333;
  text-align: center;
  margin: 20px auto;
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
}

.button, .view__more {
  cursor: pointer;
  width: 220px;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #ef0752;
  border: 0;
  border-radius: 8px;
  margin: 0 auto;
  padding: 20px 0;
  font-size: 18px;
  font-weight: 900;
  text-decoration: none;
  display: block;
}

.button:hover, .view__more:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.view__more {
  margin-top: 20px;
}

.answers-list {
  flex-wrap: wrap;
  margin: -15px 0 0 -15px;
  padding: 0;
  list-style: none;
  display: flex;
}

.answers__item {
  cursor: pointer;
  border-radius: 5px;
  flex-basis: calc(25% - 15px);
  margin-top: 15px;
  margin-left: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.answers__item:hover {
  box-shadow: 0 0 16px -1px #000;
}

.step {
  text-align: center;
  margin-bottom: 10px;
}

.answers__item--img {
  border-radius: 5px 5px 0 0;
  margin: 0 0 10px;
}

.answers__item p {
  text-align: center;
  margin-bottom: 10px;
}

.age-input {
  width: 100%;
  border: inherit;
  margin-bottom: 15px;
  padding: 5px;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.description__analyzer {
  color: #1c1c1c;
  text-align: center;
  margin-bottom: 12px;
  font-size: 16px;
  font-weight: 400;
}

.loading {
  width: 100%;
  position: relative;
}

.loading__bar {
  width: 0;
  height: 18px;
  background-image: linear-gradient(270deg, #ef0752 0%, #f10f44 100%);
  border-radius: 9px;
  transition: width .2s;
  position: absolute;
}

.loading__analyzing {
  color: #1c1c1c;
  text-transform: uppercase;
  text-align: center;
  margin-top: 50px;
  font-size: 26px;
  font-weight: 400;
}

.loading__spinner {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
}

.lds-ring {
  width: 80px;
  height: 80px;
  display: inline-block;
  position: relative;
}

.lds-ring div {
  box-sizing: border-box;
  width: 64px;
  height: 64px;
  border: 8px solid rgba(0, 0, 0, 0);
  border-top-color: #f10f44;
  border-radius: 50%;
  margin: 8px;
  animation: lds-ring 1.2s cubic-bezier(.5, 0, .5, 1) infinite;
  display: block;
  position: absolute;
}

.lds-ring div:nth-child(1) {
  animation-delay: -.45s;
}

.lds-ring div:nth-child(2) {
  animation-delay: -.3s;
}

.lds-ring div:nth-child(3) {
  animation-delay: -.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner__description {
  color: #1c1c1c;
  text-align: center;
  margin-top: 15px;
  font-size: 16px;
  font-weight: 400;
}

.review {
  flex-flow: column;
  align-items: center;
  display: none;
}

.review__logo {
  width: 200px;
  margin: 20px 0;
}

.rating__votes {
  color: #333;
  font-size: 13px;
  font-weight: 400;
}

.review__button {
  margin: 20px 0;
}

.review__description {
  text-align: center;
}

.rating__score {
  margin-right: 10px;
}

.review__link {
  color: #0a9ce5;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.review__rating {
  color: #ef0752;
  flex-flow: row;
  margin: 10px 0;
  font-size: 20px;
  display: flex;
}

.rating__stars {
  width: 100px;
  flex-flow: row;
  display: flex;
  position: relative;
}

.stars__filled {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.rating__propose {
  margin-top: 10px;
}

.rating__propose p:first-child {
  margin-bottom: 7px;
}

.rating__propose img {
  margin-right: 5px;
}

.footer {
  width: 540px;
  margin: 0 auto;
  position: relative;
}

.footer__menu {
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__menu-item:not(:last-child) {
  margin-right: 10px;
}

.footer__menu-link {
  color: #fff;
  padding: 22px 0 20px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.footer__menu-link:hover {
  color: #fff;
  text-decoration: underline;
}

._backdrop {
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .6);
  transition: opacity .2s;
  position: fixed;
  top: 0;
  left: 0;
}

._is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal__error {
  width: 520px;
  height: 400px;
  background: #fff;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.logo__error {
  width: 100%;
  height: 100px;
  background: #fff url("logo.27288c36.png") center / 250px no-repeat;
  border-radius: 8px 8px 0 0;
}

.message__error {
  text-align: center;
  margin: 20px 0;
}

@media (max-width: 540px) {
  .container {
    margin: 0;
    padding: 30px 0;
  }

  .wrapper {
    background: none;
    padding: 0;
  }

  .wrapper:before {
    display: none;
  }

  .slider {
    height: calc(100vh - 54px);
  }

  .dating-page {
    width: 100%;
    margin-top: 10px;
  }

  .form__logo {
    width: 100%;
    height: 50px;
    background: #fff url("logo.27288c36.png") center / 250px no-repeat;
    border-radius: 8px 8px 0 0;
  }

  .slider__item {
    width: 100%;
    border: none;
    border-radius: 0;
    padding: 15px;
  }

  .main-title, .sub-title {
    margin-top: 0;
  }

  .answers-list {
    width: auto;
    padding: 0;
  }

  .loading {
    width: 100%;
  }

  .footer {
    border-top: 1px solid #000;
  }

  .footer__row {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .footer__menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer__menu-item {
    margin: 0;
  }

  .footer__menu-link {
    color: #000;
  }
}

